export default WazirxCloneScript;

export const queryData = graphql`
  query MyQuery {
    whiteLabelImg: file(relativePath: { eq: "white-label-crypto.png" }) {
      childImageSharp {
        gatsbyImageData
      }
    }
    site {
      siteMetadata {
        firestore {
          apiKey
          appId
          authDomain
          messagingSenderId
          projectId
          storageBucket
        }
      }
    }
    allMetaJson(
      filter: { slug: { eq: "white-label-crypto-exchange-development" } }
    ) {
      nodes {
        metaTitle
        metaKeyword
        title
        slug
        description
      }
    }
  }
`;
